<template>
  <el-menu
    :default-active="'/index'"
    router
    mode="horizontal"
    background-color="white"
    text-color="#222"
    active-text-color="red"
    style="min-width: 1500px">
    <el-menu-item v-for="(item,i) in navList" :key="i" :index="item.name">
      {{ item.navItem }}
      <el-submenu v-if="item.children != null" v-for="(sub,j) in item.children" :key = "j" :index="sub.name">
        <el-menu-item>{{ sub.navItem }}</el-menu-item>
      </el-submenu>
    </el-menu-item>
    <span style="position: absolute;padding-top: 20px;
    right: 38%;font-size: 20px;font-weight: bold">Welcome to Dz-blogs</span>
    <el-input
      placeholder="快速搜索..."
      prefix-icon="el-icon-search"
      size="medium"
      style="width: 300px;position:absolute;margin-top: 12px;right: 18%">
    </el-input>
    <i class="el-icon-menu" style="float:right;font-size: 25px;color: #222;padding-top: 8px"></i>

    <el-dropdown style="float:right;padding-top: 8px;color:black">
       <div>
         <img :src="headUrl" class="avatar" style="width:50px;border-radius: 15px;border:1px solid #eaeaea">
         <span class="el-dropdown-link" v-html="uName"></span>
       </div>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item @click.native="personalMgr()">个人中心</el-dropdown-item>
        <el-dropdown-item>修改密码</el-dropdown-item>
        <el-dropdown-item @click.native="logout()">退出</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <el-dialog title="个人信息" :visible.sync="dialogFormVisible">
      <el-form :model="personForm" type = "multipart/form-data">
        <el-form-item label="用户名" :label-width="formLabelWidth">
          <el-input v-model="personForm.username" autocomplete="off" disabled = disabled ></el-input>
        </el-form-item>
        <el-form-item label="头像" :label-width="formLabelWidth">
          <el-upload
            class="avatar-uploader"
            :action="uploadUrl"
            :accept="accept"
            :data="uploadData"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload">
            <img v-if="imageUrl" :src="imageUrl" class="avatar" style="width:150px;border-radius: 15px;border:1px solid #eaeaea;">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="简介" :label-width="formLabelWidth">
          <el-input type="textarea" v-model="personForm.intro" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="updateUser()">确 定</el-button>
      </div>
    </el-dialog>
  </el-menu>
</template>

<script>
export default {
  inject: ['reload'],
  name: 'NavMenu',
  data () {
    return {
      navList: [
        {name: '/index', navItem: '首页'},
        {name: '/jotter', navItem: 'java'},
        {name: '/library', navItem: '数据库'},
        {name: '/admin', navItem: '中间件'}
      ],
      uName: JSON.parse(localStorage.getItem('user')).username,
      personForm: {
        username: JSON.parse(localStorage.getItem('user')).username,
        profile: JSON.parse(localStorage.getItem('user')).profile,
        intro: JSON.parse(localStorage.getItem('user')).intro
      },
      dialogFormVisible: false,
      formLabelWidth: '60px',
      headUrl: JSON.parse(localStorage.getItem('user')).profile,
      imageUrl: '',
      dialogVisible: false,
      accept: 'image/jpeg,image/gif,image/png,image/bmp',
      uploadUrl: 'http://up.imgapi.com/',
      uploadData: {
        aid: 1650325,
        Token: '3a3a1710792af8c68a66891cad051e1e10893148:HY-IZuvdNCFKs7vfD3cg9V60tCU=:eyJkZWFkbGluZSI6MTU3NTUxMTE0NiwiYWN0aW9uIjoiZ2V0IiwidWlkIjoiNzA1MjIzIiwiYWlkIjoiMTY1MDMyNSIsImZyb20iOiJmaWxlIn0='
      },
      tempUrl: ''
    }
  },
  created () {
    // console.log(localStorage.getItem('user'))
  },
  methods: {
    // 注销
    logout () {
      console.log('logout')
      this.$confirm('确定要退出吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$axios.post('/logout').then((successRes) => {
          // 退出清除登录信息
          this.$store.commit('logout')
          this.$message({message: successRes.data.msg, type: 'success' })
          this.$router.replace('/login')
        })
      })
    },
    handleAvatarSuccess (res, file) {
      this.imageUrl = URL.createObjectURL(file.raw)
      this.tempUrl = file.response.linkurl
      // console.log(this.personForm.profile)
    },
    beforeAvatarUpload (file) {
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      return isLt2M
    },
    personalMgr () {
      let _this = this
      // 查询用户信息
      let uName = JSON.parse(localStorage.getItem('user')).username
      _this.$axios.post('/queryByUserName', {username: uName}).then(successRes => {
        if (successRes.status === 200) {
          let user = successRes.data.data
          _this.personForm = {username: user.username,
            intro: user.intro}
          _this.imageUrl = user.profile
          _this.dialogFormVisible = true
        }
      }).catch(error => {
        _this.$message.error(error)
      })
    },
    updateUser () {
      const _this = this
      let reqUser = this.personForm
      this.$axios.post('/update', {username: reqUser.username, intro: reqUser.intro, profile: _this.tempUrl})
        .then(res => {
          if (res.status === 200) {
            _this.$message({message: '修改成功', type: 'success', duration: 1000})
            // 更新
            _this.dialogFormVisible = false
            location.reload()
            _this.headUrl = res.data.data.profile
          } else {
            _this.$message.error('修改失败')
          }
        }).catch(error => {
          _this.$message.error(error)
        })
    }
  }
}
</script>

<style scoped>
  .el-dropdown-link {
    cursor: pointer;
    color: black;
    margin-right: 30px;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
  a{
    text-decoration: none;
  }
</style>



